import { HttpClient } from '@angular/common/http';
import { QueriesService } from './../../_services/queries.service';
import { AuthService } from 'src/app/_services/auth.service';
import { trimValidator } from 'src/app/_utils/formValidationUtils';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';

import { passwordConfirmValidatorPartPasswordConfirm, passwordConfirmValidatorPartPassword } from '../../_utils/formValidationUtils';
import { YsToastService } from 'src/app/_services/ys-toast.service';

@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.scss']
})
export class EditUserComponent implements OnInit {

  userForm = new FormGroup({
    firstName: new FormControl(this.auth.user.firstName, [Validators.required, Validators.maxLength(128), Validators.minLength(2), trimValidator]),
    lastName: new FormControl(this.auth.user.lastName, [Validators.required, Validators.maxLength(128), Validators.minLength(2), trimValidator])
  });
  passwordForm = new FormGroup({
    currentPassword: new FormControl('', [Validators.required, Validators.maxLength(16), Validators.minLength(6)]),
    newPassword: new FormControl('', [Validators.required, Validators.maxLength(16), Validators.minLength(6)])
  });

  constructor(
    private auth: AuthService,
    private queries: QueriesService,
    private http: HttpClient,
    private ysToast: YsToastService
  ) { }

  ngOnInit(): void {
    this.passwordForm.addControl("repeatPassword", new FormControl('', [
      passwordConfirmValidatorPartPasswordConfirm.bind(this, this.passwordForm.controls.newPassword)
    ]));
    this.passwordForm.controls.newPassword.setValidators([
      Validators.required, Validators.maxLength(128), Validators.minLength(6), trimValidator,
      passwordConfirmValidatorPartPassword.bind(this, this.passwordForm.controls.repeatPassword)
    ]);
  }

  saveInfo() {
    if (this.userForm.invalid || !this.isChangedInfo())
      return;

    let body = {
      firstName: this.userForm.controls.firstName.value,
      lastName: this.userForm.controls.lastName.value
    }

    this.http.put(this.queries.urls.user, body, { params: { param: 'info' } }).subscribe((data: any) => {
      if (data) {
        this.userForm.controls.firstName.setValue(data.firstName);
        this.userForm.controls.lastName.setValue(data.lastName);
        this.auth.user.firstName = data.firstName;
        this.auth.user.lastName = data.lastName;
        this.userForm.enable();
        console.log('DATA: ', data);
      }
    });
    this.userForm.disable();

  }

  isChangedInfo() {
    return this.userForm.controls.firstName.value !== this.auth.user.firstName ||
      this.userForm.controls.lastName.value !== this.auth.user.lastName;
  }

  changePassword() {
    if (this.passwordForm.invalid)
      return;

    let body = {
      oldPassword: this.passwordForm.controls.currentPassword.value,
      newPassword: this.passwordForm.controls.newPassword.value
    }

    this.http.post(this.queries.urls.password, body, { params: { param: 'info' } }).subscribe((data: any) => {
      console.log('DATA: ', data);
      if (data) {
        localStorage.setItem('token', data.token);
        this.passwordForm.enable();
        this.passwordForm.reset();
        this.ysToast.showMessage({
          title: 'Success',
          text: 'The password has been successfully changed!',
          typeToast: 'success',
          duration: 2000
        });
      }
    }, (error) => {
      this.passwordForm.enable();
      if (error.status == 403) {
        this.ysToast.showMessage({
          title: 'Error',
          text: 'The current password was entered incorrectly!',
          typeToast: 'error',
          duration: 2000
        });
      }
    });
    this.passwordForm.disable();
  }

}
